import React, { useState } from 'react';
import axios from 'axios';
import { auth } from '../firebase';

const Login = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [phone, setPhone] = useState('');
    const [code, setCode] = useState('');
    const [step, setStep] = useState(1);

    const handleLogin = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post('http://localhost:8000/api/login', {
                email,
                password,
            });
            setPhone(response.data.phone);
            setStep(2);
        } catch (error) {
            console.error(error);
        }
    };

    const handle2FA = async (e) => {
        e.preventDefault();
        try {
            const confirmationResult = await auth.signInWithPhoneNumber(phone, window.recaptchaVerifier);
            const result = await confirmationResult.confirm(code);
            console.log(result);
            // Validate 2FA code with backend
            const response = await axios.post('http://localhost:8000/api/2fa/validate', {
                phone,
                code,
            });
            console.log(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <div>
            {step === 1 && (
                <form onSubmit={handleLogin}>
                    <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} placeholder="Email" />
                    <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="Password" />
                    <button type="submit">Login</button>
                </form>
            )}
            {step === 2 && (
                <form onSubmit={handle2FA}>
                    <input type="text" value={code} onChange={(e) => setCode(e.target.value)} placeholder="2FA Code" />
                    <button type="submit">Verify</button>
                </form>
            )}
            <div id="recaptcha-container"></div>
        </div>
    );
};

export default Login;
